"use client"

import { useEffect } from "react"
import { Users, Tractor, DollarSign, TrendingUp } from "lucide-react"
import StatCard from "./StatCard"
import { motion } from "framer-motion"
import { useLGA } from "@/context/LgaContext"
import {
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  AreaChart,
  Area,
} from "recharts"
interface LoanDataItem {
  name: string
  value: number
  color: string
  percentage: number
}

export default function Dashboard() {
  const { dashboard, loading, error, fetchDashboard } = useLGA()

  useEffect(() => {
    fetchDashboard()
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-700"></div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <p className="text-red-600 font-semibold mb-2">Error loading dashboard</p>
          <p className="text-gray-600">{error}</p>
        </div>
      </div>
    )
  }

  if (!dashboard) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p className="text-gray-600">No dashboard data available</p>
      </div>
    )
  }

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case "approved":
      case "verified":
      case "active":
        return "bg-chart-1/10 text-chart-1"
      case "pending":
        return "bg-chart-4/10 text-chart-4"
      default:
        return "bg-muted text-muted-foreground"
    }
  }

  const loanData: LoanDataItem[] = dashboard.loanDistribution || [
  { name: "Pending", value: dashboard.pendingLoans || 0, color: "#fb923c", percentage: 0 },
  { name: "Approved", value: dashboard.approvedLoans || 0, color: "#10b981", percentage: 0 },
  { name: "Disbursed", value: dashboard.disbursedLoans || 0, color: "#f43f5e", percentage: 0 },
  { name: "Rejected", value: dashboard.rejectedLoans || 0, color: "#3b82f6", percentage: 0 },
]

const totalLoans = loanData.reduce((sum, item) => sum + item.value, 0)
const loanDataWithPercentages = loanData.map(item => ({
  ...item,
  percentage: totalLoans > 0 ? Math.round((item.value / totalLoans) * 100) : 0,
}))
  // Transform backend verification data
  const verificationData = dashboard.verificationTrend || []

  // Transform backend revenue data
  const revenueData = dashboard.revenueTrend || []

  // Recent activities from backend
  const activities = dashboard.recentActivities || []

// eslint-disable-next-line @typescript-eslint/no-explicit-any
  const renderCustomLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent }: any) => {
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5
    const x = cx + radius * Math.cos((-midAngle * Math.PI) / 180)
    const y = cy + radius * Math.sin((-midAngle * Math.PI) / 180)

    return (
      <text
        x={x}
        y={y}
        fill="white"
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
        className="font-bold text-[8px]"
      >
        {`${(percent * 100).toFixed(0)}%`}
      </text>
    )
  }
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  const CustomTooltip = ({ active, payload }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-card border border-border rounded-lg p-3 shadow-lg">
          <p className="text-sm font-semibold text-foreground">{payload[0].payload.name || payload[0].payload.month}</p>
          {/* eslint-disable-next-line @typescript-eslint/no-explicit-any */}
          {payload.map((entry: any, index: number) => (
            <p key={index} className="text-sm text-muted-foreground">
              {entry.name}: <span className="font-bold text-foreground">{entry.value.toLocaleString()}</span>
            </p>
          ))}
        </div>
      )
    }
    return null
  }

  return (
    <div className="space-y-8 p-8">
      <motion.div initial={{ opacity: 0, y: -20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }}>
        <h2 className="text-3xl font-bold text-foreground mb-2">Dashboard Overview</h2>
        <p className="text-muted-foreground">Monitor your LGA agricultural activities</p>
      </motion.div>

      <motion.div
        initial="hidden"
        animate="visible"
        variants={{
          hidden: { opacity: 0 },
          visible: {
            opacity: 1,
            transition: {
              staggerChildren: 0.1,
            },
          },
        }}
        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6"
      >
        <StatCard
          icon={Users}
          label="Total Farmers"
          value={(dashboard.totalFarmers || 0).toLocaleString()}
          trend={dashboard.trends?.farmers || "+0%"}
          trendUp={true}
          color="emerald"
        />
        <StatCard
          icon={Tractor}
          label="Active Farms"
          value={(dashboard.activeFarms || 0).toLocaleString()}
          trend={dashboard.trends?.farms || "+0%"}
          trendUp={true}
          color="blue"
        />
        <StatCard
          icon={DollarSign}
          label="Loans Disbursed"
          value={dashboard.loansDisbursed || "â‚¦0"}
          trend={dashboard.trends?.loans || "+0%"}
          trendUp={true}
          color="amber"
        />
        <StatCard
          icon={TrendingUp}
          label="Repayment Rate"
          value={`${dashboard.repaymentRate || 0}%`}
          trend={dashboard.trends?.repayment || "+0%"}
          trendUp={true}
          color="rose"
        />
      </motion.div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <motion.div
          initial={{ opacity: 0, x: -20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="bg-white rounded-2xl border border-gray-200 p-6 shadow-sm hover:shadow-md transition-shadow duration-300"
        >
          <h3 className="text-lg font-semibold text-gray-900 mb-6">Loan Distribution</h3>
          {totalLoans > 0 ? (
            <>
              <ResponsiveContainer width="100%" height={300}>
                <PieChart>
                  <Pie
                    data={loanDataWithPercentages}
                    cx="50%"
                    cy="50%"
                    labelLine={false}
                    label={renderCustomLabel}
                    outerRadius={110}
                    innerRadius={65}
                    fill="#8884d8"
                    dataKey="value"
                    animationBegin={0}
                    animationDuration={800}
                    paddingAngle={3}
                    stroke="#fff"
                    strokeWidth={2}
                  >
                    {loanDataWithPercentages.map((entry, index) => (
                      <Cell
                        key={`cell-${index}`}
                        fill={entry.color}
                        className="hover:opacity-80 transition-opacity cursor-pointer"
                      />
                    ))}
                  </Pie>
                  <Tooltip content={<CustomTooltip />} />
                </PieChart>
              </ResponsiveContainer>
              <div className="space-y-3 mt-4">
                {loanDataWithPercentages.map((item, index) => (
                  <motion.div
                    key={item.name}
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: 0.8 + index * 0.1 }}
                    className="flex items-center justify-between p-3 rounded-xl hover:bg-gray-50 transition-colors duration-200 cursor-pointer group"
                  >
                    <div className="flex items-center gap-3">
                      <motion.div
                        whileHover={{ scale: 1.2 }}
                        style={{ backgroundColor: item.color }}
                        className="w-4 h-4 rounded-full shadow-sm"
                      />
                      <span className="text-sm font-medium text-gray-900">{item.name}</span>
                    </div>
                    <div className="flex items-center gap-4">
                      <span className="text-sm text-gray-600">{item.percentage}%</span>
                      <span className="text-sm font-bold text-gray-900 w-8 text-right group-hover:scale-110 transition-transform">
                        {item.value}
                      </span>
                    </div>
                  </motion.div>
                ))}
              </div>
            </>
          ) : (
            <div className="flex items-center justify-center h-[300px] text-gray-500">
              No loan data available
            </div>
          )}
        </motion.div>

        <motion.div
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5, delay: 0.3 }}
          className="bg-white rounded-2xl border border-gray-200 p-6 shadow-sm hover:shadow-md transition-shadow duration-300"
        >
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Farm Verification Trend</h3>
          {verificationData.length > 0 ? (
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={verificationData} barGap={8}>
                <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" className="opacity-30" vertical={false} />
                <XAxis 
                  dataKey="month" 
                  stroke="#6b7280" 
                  tick={{ fill: "#6b7280" }} 
                  style={{ fontSize: "12px" }}
                  axisLine={{ stroke: "#d1d5db" }}
                />
                <YAxis 
                  stroke="#6b7280" 
                  tick={{ fill: "#6b7280" }} 
                  style={{ fontSize: "12px" }}
                  axisLine={{ stroke: "#d1d5db" }}
                />
                <Tooltip content={<CustomTooltip />} cursor={{ fill: "#f3f4f6", opacity: 0.3 }} />
                <Legend 
                  wrapperStyle={{ paddingTop: "20px" }}
                  iconType="circle"
                />
                <Bar 
                  dataKey="verified" 
                  fill="#1E580D" 
                  radius={[8, 8, 0, 0]} 
                  animationDuration={800}
                  maxBarSize={50}
                />
                <Bar 
                  dataKey="pending" 
                  fill="#FFCD00" 
                  radius={[8, 8, 0, 0]} 
                  animationDuration={800}
                  maxBarSize={50}
                />
              </BarChart>
            </ResponsiveContainer>
          ) : (
            <div className="flex items-center justify-center h-[300px] text-gray-500">
              No verification data available
            </div>
          )}
        </motion.div>
      </div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.4 }}
        className="bg-white rounded-2xl border border-gray-200 p-6 shadow-sm hover:shadow-md transition-shadow duration-300"
      >
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Revenue vs Target</h3>
        {revenueData.length > 0 ? (
          <ResponsiveContainer width="100%" height={300}>
            <AreaChart data={revenueData}>
              <defs>
                <linearGradient id="colorRevenue" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#0F3D06" stopOpacity={0.8} />
                  <stop offset="95%" stopColor="#0F3D06" stopOpacity={0.1} />
                </linearGradient>
                <linearGradient id="colorTarget" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#D59B00" stopOpacity={0.8} />
                  <stop offset="95%" stopColor="#D59B00" stopOpacity={0.1} />
                </linearGradient>
              </defs>
              <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" className="opacity-30" vertical={false} />
              <XAxis 
                dataKey="month" 
                stroke="#6b7280" 
                tick={{ fill: "#0F3D06" }}  
                style={{ fontSize: "12px" }}
                axisLine={{ stroke: "#d1d5db" }}
              />
              <YAxis 
                stroke="#6b7280" 
                tick={{ fill: "#6b7280" }} 
                style={{ fontSize: "12px" }}
                axisLine={{ stroke: "#d1d5db" }}
              />
              <Tooltip content={<CustomTooltip />} />
              <Legend 
                wrapperStyle={{ paddingTop: "20px" }}
                iconType="circle"
              />
              <Area
                type="monotone"
                dataKey="revenue"
                stroke="#0F3D06" 
                strokeWidth={3}
                fillOpacity={1}
                fill="url(#colorRevenue)"
                animationDuration={1000}
                dot={{ fill: "#0F3D06", strokeWidth: 2, r: 4 }}
                activeDot={{ r: 6, fill: "#0F3D06", stroke: "#fff", strokeWidth: 2 }}
              />
              <Area
                type="monotone"
                dataKey="target"
                stroke="#D59B00"  
                strokeWidth={3}
                fillOpacity={1}
                fill="url(#colorTarget)"
                animationDuration={1000}
                dot={{ fill: "#D59B00", strokeWidth: 2, r: 4 }}
                activeDot={{ r: 6, fill: "#D59B00", stroke: "#fff", strokeWidth: 2 }}
              />
            </AreaChart>
          </ResponsiveContainer>
        ) : (
          <div className="flex items-center justify-center h-[300px] text-gray-500">
            No revenue data available
          </div>
        )}
      </motion.div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.5 }}
        className="bg-white rounded-2xl border border-gray-200 p-6 shadow-sm hover:shadow-md transition-shadow duration-300"
      >
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-lg font-semibold text-gray-900">Recent Activities</h3>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="text-sm font-medium text-green-700 hover:text-green-600 transition-colors"
          >
            View All
          </motion.button>
        </div>
        {activities.length > 0 ? (
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-200">
                  <th className="text-left py-3 px-4 text-xs font-semibold text-gray-600 uppercase">Farmer</th>
                  <th className="text-left py-3 px-4 text-xs font-semibold text-gray-600 uppercase">Activity</th>
                  <th className="text-left py-3 px-4 text-xs font-semibold text-gray-600 uppercase">Date</th>
                  <th className="text-left py-3 px-4 text-xs font-semibold text-gray-600 uppercase">Status</th>
                </tr>
              </thead>
            <tbody className="divide-y divide-gray-200">
  {activities.map((activity: any, index: number) => (
    <motion.tr
      key={index}
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ delay: 0.6 + index * 0.1 }}
      className="hover:bg-gray-50 transition-colors duration-200 cursor-pointer"
    >
      {/* Farmer Name */}
      <td className="py-3 px-4 text-sm font-medium text-gray-900">
        {activity.data?.fullname || "Unknown Farmer"}
      </td>

      {/* Activity Message */}
      <td className="py-3 px-4 text-sm text-gray-600">
        {activity.message}
      </td>

      {/* Date */}
      <td className="py-3 px-4 text-sm text-gray-600">
        {new Date(activity.createdAt).toLocaleDateString("en-US", {
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "2-digit",
          minute: "2-digit"
        })}
      </td>

      {/* Activity Type (converted to readable form) */}
      <td className="py-3 px-4">
        <motion.span
          whileHover={{ scale: 1.05 }}
          className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-emerald-100 text-emerald-700"
        >
          {activity.type.replace(/_/g, " ")} 
        </motion.span>
      </td>
    </motion.tr>
  ))}
</tbody>

            </table>
          </div>
        ) : (
          <div className="flex items-center justify-center py-8 text-gray-500">
            No recent activities
          </div>
        )}
      </motion.div>
    </div>
  )
}